.pricing{
    width: 100%;
    padding: 6rem;
    background-color: #2d67c5;

   
}
.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr );
   
    
}
.card{
    border: 1px solid #EEEEEE;
    color: #EEEEEE;
    text-align: center;
    padding: 1rem;
    border-radius: 15px;
    margin: 10px;
   
}
.card:hover{
    background-color:#222831;
}
.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}
.card p{
    
    padding: 10px 0;
}

.bar{
    border-bottom: 1px solid #EEEEEE;
    width: 30%;
    margin: 1.5rem auto;
    display: block;
}

.btc{
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0 ;
}

.card-container .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}



@media screen and (max-width:740px) {
    .card-container{
        max-width: 100%;
       
        grid-template-columns: 1fr ;
    }
    
}